@import "../../colors";

@media screen and (min-width: 760px) {
  .heading-bpmft {
    grid-row: 1 / 2;
    text-align: center;
    font-size: 2em;
    font-family: "Viga", sans-serif;
    color: $red;
    text-shadow: 0 2px 10px rgb(255, 255, 255);
  }
  .flex-container {
    grid-row: 2 / 3;
    display: flex;
    flex-flow: row nowrap;
    .row {
      flex: 1 1 auto;
      &:nth-child(1) {
        margin-right: 10px;
      }
      &:nth-child(2) {
        margin-left: 10px;
      }
    }
  }
}
