@import "../../colors";

.flex-column {
  flex-flow: column nowrap;
  background-color: white;
  border-radius: 10px;
  padding: 10px;
  margin-bottom: 10px;
  box-shadow: 0 2px 16px rgba(0, 0, 0, 0.25);
}

.name {
  margin: 5px 0;
  text-align: center;
}

.image {
  display: flex;
  justify-content: center;
  img {
    max-width: 200px;
    max-height: 200px;
    margin-bottom: 10px;
    border-radius: 10px;
  }
}

.btn-group {
  display: flex;
  flex-flow: row wrap;
  .btn {
    flex: 1 1 auto;
    padding: 15px 5px;
    border-radius: 10px;
    background-color: white;
  }

  .up {
    border: 2px solid $blue;
    margin-right: 5px;
    &:hover {
      background-color: $blue;
      color: white;
    }
    &:active {
      background-color: $darkBlue;
      color: white;
    }
  }

  .down {
    background-color: $lightRed;
    border: 2px solid $lightRed;
    color: white;
    margin-left: 5px;
    &:hover {
      background-color: $red;
      border: 2px solid $red;
    }
    &:active {
      background-color: $darkRed;
      border: 2px solid $darkRed;
    }
  }
}

@media screen and (min-width: 760px) {
  .flex-column {
    flex-flow: column nowrap;
