      }
    }

    .down {
      background-color: $lightRed;
      border: 2px solid $lightRed;
      color: white;
      margin-left: 5px;
      &:hover {
        background-color: $red;
        border: 2px solid $red;
      }
      &:active {
        background-color: $darkRed;
        border: 2px solid $darkRed;
      }
    }
  }
}
