@import url(https://fonts.googleapis.com/css?family=Open+Sans|Viga);
.flex-column {
  flex-flow: column nowrap;
  background-color: white;
  border-radius: 10px;
  padding: 10px;
  margin-bottom: 10px;
  box-shadow: 0 2px 16px rgba(0, 0, 0, 0.25); }

.name {
  margin: 5px 0;
  text-align: center; }

.image {
  display: flex;
  justify-content: center; }
  .image img {
    max-width: 200px;
    max-height: 200px;
    margin-bottom: 10px;
    border-radius: 10px; }

.btn-group {
  display: flex;
  flex-flow: row wrap; }
  .btn-group .btn {
    flex: 1 1 auto;
    padding: 15px 5px;
    border-radius: 10px;
    background-color: white; }
  .btn-group .up {
    border: 2px solid #1565c0;
    margin-right: 5px; }
    .btn-group .up:hover {
      background-color: #1565c0;
      color: white; }
    .btn-group .up:active {
      background-color: #0d47a1;
      color: white; }
  .btn-group .down {
    background-color: #c44343;
    border: 2px solid #c44343;
    color: white;
    margin-left: 5px; }
    .btn-group .down:hover {
      background-color: #c62828;
      border: 2px solid #c62828; }
    .btn-group .down:active {
      background-color: #b71c1c;
      border: 2px solid #b71c1c; }

