body {
  font-family: "Open Sans", sans-serif; }

.container {
  display: grid;
  margin: 0 auto;
  padding: 0 10px;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(4, minmax(50px, auto));
  grid-gap: 10px; }

.header {
  color: black;
  text-align: center;
  font-size: 2em;
  font-family: "Viga", sans-serif;
  grid-row: 1 / 2; }

.main {
  grid-row: 2 / 3;
  display: flex;
  flex-flow: column wrap; }

.footer {
  grid-row: 4 / 5;
  color: #455a64; }
  .footer p {
    text-align: center;
    align-self: center; }

@media screen and (min-width: 760px) {
  body {
    margin: 0; }
  .container {
    display: grid;
    max-width: 1200px;
    margin: 0 auto;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(4, minmax(50px, auto));
    grid-gap: 10px; }
  .header {
    color: black;
    text-align: center;
    font-size: 2em;
    font-family: "Viga", sans-serif;
    grid-row: 1 / 2;
    margin-top: 20px; }
  .main {
    grid-row: 2 / 3;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(2, minmax(50px, auto));
    grid-gap: 20px; }
    .main .smft {
      grid-row: 1 / 2; }
    .main .bpmft {
      grid-row: 2 / 3; }
    .main .grid {
      display: grid;
      grid-template-columns: 1fr;
      grid-template-rows: repeat(2, minmax(50px, auto));
      background-color: white;
      padding: 15px;
      border-radius: 10px;
      box-shadow: 0 2px 16px rgba(0, 0, 0, 0.5); }
  .invalid-vote {
    grid-row: 3 / 4; }
  .footer {
    grid-row: 4 / 5;
    color: #455a64; }
    .footer p {
      text-align: center;
      align-self: center; } }

@media screen and (min-width: 760px) {
  .heading-smft {
    grid-row: 1 / 2;
    text-align: center;
    font-size: 2em;
    font-family: "Viga", sans-serif;
    color: #ffd700;
    text-shadow: 0 2px 10px white; }
  .flex-container {
    grid-row: 2 / 3;
    display: flex;
